@import '../../variaveis.css';

nav {
    height: 90px;
    width: 100vw;
    background-color: var(--cor-1);
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    position: fixed;
    padding-bottom: 15px;
    z-index: 10;
}

nav .nav-menu {
    height: fit-content;
    height: fit-content;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    gap: 10px;
}

nav .nav-menu .nav-menu-itens {
    list-style: none;
    transition: var(--transition-original);    
}
    nav .nav-menu .nav-menu-itens:hover {
        transition: var(--transition-hover);
        transform: var(--transition-scale);
    }

nav .nav-menu .nav-menu-itens a {
    text-decoration: none;
    padding: 5px 15px;
    color: var(--cor-white);
    font-size: var(--text-size-small);
}

nav .item-destaque-menu {
    background-color: var(--cor-white);
    color: var(--cor-1) !important;
}

nav .link-img-nav {
    width: fit-content;
    height: fit-content;
    display: flex;
    align-items: flex-end;
}

nav .link-img-nav .img-nav {
    height: 35px;
    transition: var(--transition-original);
}

nav .link-img-nav .img-nav:hover {
    transition: var(--transition-hover);
    transform: var(--transition-scale);
}

nav .btn-menu {
    display: none;
}


@media (max-width: 900px) {

    nav {
        position: relative;
    }

    nav .nav-menu {
        width: 100%;
        position: absolute;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 20px;
        background-color: var(--cor-1);
        padding: 30px;
        
        top: 0px;
        opacity: 0;
        z-index: -1;
        pointer-events: none;
        transition: var(--transition-original);
    }

    .nav-menu-active .nav-menu {
        top: 90px;
        opacity: 1;
        z-index: -1;
        pointer-events: all;
        transition: var(--transition-hover);
    }

    nav .btn-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 7px;
        background-color: transparent;
        border: none;
        width: 40px;
        height: 40px;
    }

    nav .btn-menu .line {
        width: 50px;
	    height: 7px; 
	    background-color: var(--cor-white);
	    border-radius: 5px;
        transition: all .5s;
    }

        .nav-menu-active .btn-menu .line1 {
            transform: translate3d(0,14px,0) rotate(-315deg);
        }

        .nav-menu-active .btn-menu .line2 {
            transform: scale(0);
         }

        .nav-menu-active .btn-menu .line3 {
            transform: translate3d(0,-15px,0) rotate(-225deg);
         }
